@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    margin: 0;
    padding: 0;
    background-color: #242020;
}

.navbar, .icon{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.navbar{
    justify-content: space-around;
    height: 85px;
    background-color: #292828;
}

.icon{
    gap: 10px;

}

.image,.icon-text-container,.icon,.primary,.secondary{
    background-color: transparent;
}

.image img{
    background-color: #292828;
}

.primary{
    font-size: 30px;
    color: white;
}

.secondary{
    color: white;
    font-weight: 100;
}


.search{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

input, button{
    height: 32px;
    width: 300px;
}

input{

    background-color:#373737 ;
    color: #7F7F7F;
    padding-left:5px ;
    border:none ;
    outline: none;
}

button{
    width: fit-content;
    padding: 0 10px 0 10px;
    background-color: transparent;
    border: 1px solid #949393;
    color: #949393;
    cursor: pointer;
}

.icons-right{
    display: flex;
    gap: 5px;
    align-items: end;
    background-color: transparent;
}

.icon-image-right{
    cursor: pointer;
    background-color: transparent;
}

.home{
    max-width: 1200px;
    margin-inline: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
    gap: 10px;
}

.thumbnail img{
    width: 200px;
    height: 300px;
    object-fit: cover;
    margin: 5px;
}

.rendered-image{
    cursor: pointer;
}

.renderBook{
    max-width: 1200px;
    height: 400px;
    margin-inline: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
}

.book-details{
    width: 200px;
    height: 300px;
}


.details-container{
    height: 300px;
    background-color: #415F71;
    border-radius: 0 20px 20px 0;
}

.title-desc-container{
    height: 80%;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    color: white;
    padding: 20px;
    background-color: transparent;
    text-align:justify;
    overflow-y: scroll;
}

.title-desc-container::-webkit-scrollbar{
    display: none;
}

.button-container{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
}

.button-container button {
    width:  150px;
    height: 50px;
    font-size: 18px;
    border-color:white ;
    color: white;
    margin: 5px;
}

.title{
    font-size: 30px;
    background-color: transparent;
}

.description{
    background-color: transparent;
}

.text{
    color: white;
    padding: 30px;
    text-align: center;
}

a{
    text-decoration: none;
    background-color: #415F71;
    color: white;
}